export default {
  data: function data() {
    return {
      xieyiUrl: '/static/html/agreement.html',
      showAgreement: false,
      agreementType: 1,
      mapper: {
        1: '儿童个人信息保护规则',
        2: '中小学生校外培训服务合同',
        3: '隐私政策',
        4: '服务承诺',
        5: '用户投诉及争议解决机制'
      }
    };
  },
  components: {},
  methods: {
    goBeian: function goBeian() {
      var popup = window.open('about:blank', '_blank');
      popup.document.write('<h2>加载中</h2>');
      popup.location = 'http://www.beian.miit.gov.cn/publish/query/indexFirst.action';
    }
  },
  deactivated: function deactivated() {
    this.showAgreement = false;
  },
  destoryed: function destoryed() {
    this.showAgreement = false;
  },
  mounted: function mounted() {
    var _this = this;

    this.$bus.$off('openXieyi');
    this.$bus.$on('openXieyi', function (type) {
      _this.showAgreement = true;
      _this.agreementType = type;
    });
  },
  created: function created() {}
};