/*****************************************************
 * JS原型扩展文件，在main.js里统一引用
 ****************************************************/
import {postWeb} from './http';

/**
 * 原型扩展函数
 * @author liubaohuo
 * @param "name" 函数名
 * @param func 函数体
 * @param {this}
 */
Function.prototype.method = function(name, func) {
    if (!this.prototype[name]) {
        this.prototype[name] = func;
    }
    return this;
}

/**
 * Array原型扩展，查找数组内符合条件的第一项的属性值
 * @author liubaohuo
 * @param filed <String> 要返回的字段名
 * @param condition <Object> 条件参数，可以多条
 * @return "属性值"
 */
Array.method('findByCondition', function(field, condition) {
    let self = this;
    let item = self.find((element) => {
        let isSelected = true;
        for (let key in condition) {
            if (element[key] !== condition[key]) {
                isSelected = false;
                break;
            }
        }
        return isSelected;
    })
    return (item && item[field]);
})

/**
 * Array原型扩展，查找数组内符合条件的所有项的属性值
 * @author liubaohuo
 * @param filed <String> 要返回的字段名
 * @param condition <Object> 条件参数，可以多条
 * @return ["属性值"]
 */
Array.method('filterByCondition', function(field, condition) {
    let self = this;
    let items = self.filter((element) => {
        let isSelected = true;
        for (let key in condition) {
            if (condition[key] instanceof Array) {
                let listTmp = condition[key].filter((subElement) => {
                    return element[key] === subElement;
                })
                if (!listTmp.length) {
                    isSelected = false;
                    break;
                }
            } else {
                if (element[key] !== condition[key]) {
                    isSelected = false;
                    break;
                }
            }
        }
        return isSelected;
    })
    let keyItems = [];
    items.forEach((item, index) => {
        keyItems.push(item[field]);
    })
    return keyItems;
})

/**
 * Array原型扩展，将数据的属性值拼接成新的数组
 * @author liubaohuo
 * @param filed <String> 要返回的字段名
 * @return ["属性值的子项"]
 */
Array.method('concatAttr', function(field) {
    let self = this;
    let listTmp = [];
    self.forEach((item, index) => {
      if(typeof(item[field]) !== "undefined"){
        if (item[field] instanceof Array) {
          item[field].forEach((subItem, subIndex) => {
              listTmp.push(subItem);
          })
        }else{
          listTmp.push(item[field]);
        }
      }
    })
    return listTmp;
})

/**
 * Array原型扩展，根据属性或下标删除数组项
 * @author liubaohuo
 * @param item <Nubmer, Object> 属性或下标
 * @return <Array> 删除数组项
 */
Array.method('remove', function(item) {
    let self = this;
    let listTmp = [];
    listTmp = self.splice(item, 1);
    return listTmp;
})

/**
 * Date原型扩展，格式化
 * @author liubaohuo
 * @param "fmt"
 * @return "格式字符串"
 */
Date.method('format', function(fmt) {
    let o = {
        "M+": this.getMonth() + 1, //月份   
        "d+": this.getDate(), //日   
        "h+": this.getHours(), //小时   
        "m+": this.getMinutes(), //分   
        "s+": this.getSeconds(), //秒   
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度   
        "S": this.getMilliseconds() //毫秒   
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
})

/**
 * String原型扩展，去除头尾空格
 * @author liubaohuo
 * @return "格式字符串"
 */
String.method('trim', function() {
    return this.replace(/(^\s*)|(\s*$)/g, "");
})