
let api = {
    getOrderList:'/order/getOrderList',//订单列表
    getOrderPayDetail:'/order/getOrderPayDetail',//订单详情
    getOrderRefundDetail:'/order/getOrderRefundDetail',//退款详情
    getPayCode:'/order/getPayCode',//获取微信支付二维码
    getOrderStatus:'/order/getOrderStatus'//获取支付状态
}
import {postWeb} from "@/assets/js/http";

/**
 * user模块
 */
export default {
    namespaced: true,
    state: {

    },
    getters: {

    },
    mutations: {

    },
    actions: {
        getOrderListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getOrderList, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getOrderPayDetailActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getOrderPayDetail, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getOrderRefundDetailActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getOrderRefundDetail, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getPayCodeActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getPayCode, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getOrderStatusActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getOrderStatus, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        }
    }
}
  