window.setLayoutUi = function () {
  vm.$nextTick(function () {
    var mainBox = document.querySelector('.main-box');

    if (mainBox) {
      var root = document.documentElement || document.body;
      var leftSider = document.querySelector('.aside-left');
      var rightSider = document.querySelector('.aside-right'); // window.console.log(mainBox.offsetWidth,root.clientWidth,'ofw');

      var distance = (root.clientWidth - mainBox.offsetWidth) / 2;
      mainBox.style.left = distance + 'px';
      vm.$store.commit('updateMainSiderValue', distance + 'px');

      if (leftSider) {
        leftSider.style.left = distance + 'px';
        vm.$store.commit('updateLeftSiderValue', leftSider.style.left);
      }

      if (rightSider) {
        rightSider.style.right = distance + 'px';
        vm.$store.commit('updateRightSiderValue', rightSider.style.right);
      }
    }

    var isMac = /macintosh|mac os x/i.test(navigator.userAgent);
    var isApple = /Safari/.test(navigator.userAgent) && !/Chrome/.test(navigator.userAgent); // 如果是mac 修改默认字体

    if (isMac || isApple) {
      var html = document.getElementsByTagName("html")[0];
      html.style.fontFamily = "Microsoft YaHei";
      window.console.log(html.style.fontFamily, 'fontFamily');
    }
  });
};

window.addEventListener('resize', function () {
  window.setLayoutUi();
}); //解决多次跳同一个路由报错

