/**
 * 阿里前端监控
 * 配置文档 https://www.npmjs.com/package/alife-logger
 */
import BrowerLogger from 'alife-logger';
import fun from '@/assets/js/fun';
import helper from '@/assets/js/helper';
import Cookie from 'cookie';
var cookie = Cookie.parse(document.cookie);
var status = 0;

if (!fun.isEmpty2(cookie)) {
  var accountInfo = cookie.accountInfo;

  if (typeof accountInfo !== 'undefined') {
    var info = JSON.parse(accountInfo);

    if (info.student_id != undefined) {
      status = 1;
    }
  }
}

var getUid = function getUid() {
  var uid = 0;

  if (status == 1) {
    var currentChildInfo = helper.cookieUtils.getItem('currentChildInfo', true);

    if (currentChildInfo && currentChildInfo.student_id !== undefined) {
      uid = currentChildInfo.student_id;
    }
  }

  return uid;
};

var __bl = BrowserLogger.singleton({
  uid: getUid(),
  pid: 'g1i6fo9yme@21a7c1de7e75962',
  // 这是项目ID，新建一个应用站点后自动生成，在单个应用站点内部的`设置->应用设置`查看
  imgUrl: 'https://arms-retcode.aliyuncs.com/r.png?',
  // 日志上传地址，源码中默认值就是这个地址
  enableSPA: true,
  // 是否监听页面的 hashchange 事件并从新上报 PV，适用于单页面应用场景
  sendResource: true,
  // 是否上报资源数据
  environment: process.env.VUE_APP_ENV,
  pvSample: 2,
  //  1/pvSample
  ignore: {
    ignoreApis: [/\/studentweb\//]
  }
});

export default __bl;