
let api = {
  getCodeFreeCreationList:'/trainingCamp/getCodeFreeCreationList',
  getCodeFreeCreationGoToUrl:'/trainingCamp/getCodeFreeCreationGoToUrl',
  deleteCodeFreeCreation:'/trainingCamp/deleteCodeFreeCreation'
}
import {postWeb} from "@/assets/js/http";

/**
 * user模块
 */
export default {
    namespaced: true,
    state: {

    },
    getters: {

    },
    mutations: {

    },
    actions: {
      getCodeFreeCreationListActions({commit,dispatch},params){
          return new Promise((resolve,reject)=>{
            postWeb(api.getCodeFreeCreationList,params).then((res)=>{
              resolve(res)
            },(err)=>{
              reject(err)
            })
          })
        },
      deleteCodeFreeCreationActions({commit,dispatch},params){
          return new Promise((resolve,reject)=>{
            postWeb(api.deleteCodeFreeCreation,params).then((res)=>{
              resolve(res)
            },(err)=>{
              reject(err)
            })
          })
        },
      getCodeFreeCreationGoToUrlActions({commit,dispatch}){
        return new Promise((resolve,reject)=>{
          postWeb(api.getCodeFreeCreationGoToUrl).then((res)=>{
            resolve(res)
          },(err)=>{
            reject(err)
          })
        })
      }
    }
}
  