
let api = {
    getTestResultList:'/test/getTestResultListWeb',//入学评测列表
    getLessonExaminationList:'/course/getLessonExaminationList',//单元评测列表
    getTestWebViewInputData:'/test/getTestWebViewInputData',//获取开始评测webview地址(入学测评)
    getTestReportWebViewInputData:'/test/getTestReportWebViewInputData',//获取测评报告webview地址(入学测评)
    getLessonExaminationReportWebViewInputData:'/course/getLessonExaminationReportWebViewInputData',//获取测评报告webview(单元测评)
    getLessonExaminationWebViewInputData:'/course/getLessonExaminationWebViewInputData',//获取开始测评webview(单元测评)
    getTestStageList:'/test/getTestStageList',//获取测评等级列表(废弃)
    getTestStage:'/test/getTestStageWeb'//获取测评入口数据
}
import {postWeb} from "@/assets/js/http";

function createParams(params){
    let newParams = {
        origin_url:window.location.origin
    }
    newParams.origin_base_url = newParams.origin_url + params.to.fullPath
    for(let key in params){
        if(key!=='to'){
            newParams[key] = params[key]
        }
    }
    return newParams
}

/**
 * user模块
 */
export default {
    namespaced: true,
    state: {

    },
    getters: {
        haha(state) {
            return state.haha;
        }
    },
    mutations: {
        updatehaha(state, payload){
            state.haha = payload
        }
    },
    actions: {
        getLessonExaminationListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getLessonExaminationList, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTestResultListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getTestResultList, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTestWebViewInputDataActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                let newParams = createParams(params)
                postWeb(api.getTestWebViewInputData, newParams).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTestReportWebViewInputDataActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                let newParams = createParams(params)
                postWeb(api.getTestReportWebViewInputData, newParams).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getLessonExaminationReportWebViewInputDataActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                let newParams = createParams(params)
                postWeb(api.getLessonExaminationReportWebViewInputData, newParams).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getLessonExaminationWebViewInputDataActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                let newParams = createParams(params)
                postWeb(api.getLessonExaminationWebViewInputData, newParams).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTestStageListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getTestStageList, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTestStageActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getTestStage, params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        }
    }
}
  