/*****************************************************
 * JS全局函数，在main.js里统一引用
 ****************************************************/

/**
 * 将Http调用方法注入全局变量，省去引用麻烦
 * @author liubaohuo
 */
import {getWeb, postWeb, uploadWeb, assignByUrl, changeBaseUrl} from '@/assets/js/http';
global.getWeb = getWeb;
global.postWeb = postWeb;
global.uploadWeb = uploadWeb;
global.assignByUrl = assignByUrl;
global.changeBaseUrl = changeBaseUrl;

/**
 * 将fun调用方法注入全局变量，省去引用麻烦
 * @author liubaohuo
 */
import fun from '@/assets/js/fun';
for(let key in fun){
  switch(key){
    case "isEmpty2":
      global.isEmpty = fun[key];
      break;
    case "debounce2":
      global.debounce = fun[key];
      break;
    default:
      global[key] = fun[key];
      break;
  }
}

/**
 * 初始化展示的iframe
 * @author liubaohuo
 * @param iframe <Element>
 */
global.initViewIframe = function(iframe){
  iframe.frameBorder = 0;
  iframe.marginHeight = "0px";
  iframe.marginWidth = "0px";
  iframe.width = "100%";
  iframe.height = iframe.contentWindow.document.body.scrollHeight;
  //iframe.contentWindow.document.body.style.fontSize = '12px';
  window.onresize = function(){  
    global.initViewIframe(iframe);  
  } 
}

/**
 * 控制台输出日志
 * @author liubaohuo
 */
global.logZYKJ = function(){
  const args = Array.from(arguments);
  if(process.env.NODE_ENV === 'development'){
    global.console.log(...args);
  }
}

