<template>
    <div class="dialog" v-show="showEwm">
        <div class="dialog-mask"></div>
        <div class="dialog-main ewm-content">
            <div class="dialog-header">
                <!-- <span class="close" @click="showEwm=false">x</span> -->
                <img src="@/assets/img/close-ico.png" @click="showEwm=false">
            </div>
            <div class="dialog-content">
                <div class="ewm-dialog-box" v-if="ewmType===1">
                    <img src="@/assets/img/xcx.jpg" alt="">
                    <p>扫码打开小程序</p>
                </div>

                <div class="ewm-dialog-box" v-if="ewmType===2">
                    <img src="@/assets/img/gzh.jpg" alt="">
                    <p>扫码关注公众号</p>
                </div>
            </div>

            <div class="dialog-bottom">
                <button class="btn btn-yellow" @click="showEwm=false">一会再说</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: () => ({
            showEwm: false,
            ewmType: 1//1小程序 2.服务号
        }),
        components: {},
        methods: {},
        mounted() {
            this.$bus.$off('openEwm')
            this.$bus.$on('openEwm', (type) => {
                this.showEwm = true
                this.ewmType = type
            })
        },
        created() {

        }
    }
</script>

<style lang="postcss" scoped>
    .ewm-dialog-box{
        padding-top: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        & img{
            width: 20rem;
            height: 20rem;
        }
        & p{
            font-size: 1.5rem;
            color: black;
            font-weight: bold;
            margin:1rem 0;
        }
    }
</style>
